* {
  box-sizing: border-box;
}

html,
body {
  background-color: black;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

.parallax {
  perspective: 100px;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  will-change: transform;
}


/* Safari iOS fix */
@supports (-webkit-touch-callout: none) {
  .parallax {
    height: -webkit-fill-available;
  }
}

.parallax__layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transform-origin: center center;
  will-change: transform;
}

/* Base styles for all images */
.parallax__layer img {
  display: block;
  position: absolute;
  max-width: none;
  height: auto;
}

/* Depth layers */
.parallax__layer__0 {
  transform: translateZ(-300px) scale(4);
}

.parallax__layer__1 {
  transform: translateZ(-250px) scale(3.5);
}

.parallax__layer__2 {
  transform: translateZ(-200px) scale(3);
}

.parallax__layer__3 {
  transform: translateZ(-150px) scale(2.5);
}

.parallax__layer__4 {
  transform: translateZ(-100px) scale(2);
}

.parallax__layer__5 {
  transform: translateZ(-50px) scale(1.5);
}

.parallax__layer__6 {
  transform: translateZ(0px) scale(1);
}

/* Desktop XL */
@media (min-width: 1200px) {
  .parallax__layer__6 img {
    width: 100%;
    left: 0%;
    top: 19vh;
    height: 100%;
  }

  .parallax__layer__5 img {
    width: 60%;
    left: 0%;
    top: -15%;
  }

  .parallax__layer__4 img {
    width: 60%;
    left: 40%;
    top: -18%;
  }

  .parallax__layer__3 img {
    width: 30%;
    left: 30%;
    top: 5%;
  }

  .parallax__layer__2 img {
    width: 20%;
    left: 67%;
    top: -5%;
  }

  .parallax__layer__1 img {
    width: 30%;
    left: 0%;
    top: -7%;
  }

  .parallax__layer__0 img {
    width: 30%;
    left: 75%;
    top: -12%;
  }
}

/* Tablet and Small Laptop */
@media (max-width: 1199px) and (min-width: 768px) {
  .parallax__layer__6 img {
    width: 100%;
    left: 0%;
    top: 0%;
    height: 100vh;
  }

  .parallax__layer__5 img {
    width: 37%;
    left: 65%;
    top: 8%;
  }

  .parallax__layer__4 img {
    width: 35%;
    left: 35%;
    top: 8%;
  }

  .parallax__layer__3 img {
    width: 35%;
    left: 0%;
    top: 8%;
  }

  .parallax__layer__2 img {
    width: 12%;
    left: 65%;
    top: 10%;
  }

  .parallax__layer__1 img {
    width: 30%;
    left: 70%;
    top: -5%;
  }

  .parallax__layer__0 img {
    width: 35%;
    left: 30%;
    top: -10%;
  }
}

/* Universal Mobile */
@media (max-width: 767px) {
  .parallax {
    perspective: 50px;
    height: 100vh;
  }

  .parallax__layer__6 img {
    width: 100%;
    left: 0%;
    top: 0vh;
    height: 100vh;
  }

  .parallax__layer__5 img {
    width: 100%;
    left: -5%;
    top: -15%;
  }

  .parallax__layer__4 img {
    width: 100%;
    left: 40%;
    top: -25%;
  }

  .parallax__layer__3 img {
    width: 100%;
    left: 100%;
    top: -45%;
  }

  .parallax__layer__2 img {
    width: 20%;
    left: 67%;
    top: 0%;
  }

  .parallax__layer__1 img {
    width: 30%;
    left: -10%;
    top: 0%;
  }

  .parallax__layer__0 img {
    width: 30%;
    left: 75%;
    top: 0%;
  }

  .parallax__layer__0 {
    transform: translateZ(-200px) scale(3);
  }

  .parallax__layer__1 {
    transform: translateZ(-150px) scale(2.5);
  }

  .parallax__layer__2 {
    transform: translateZ(-100px) scale(2);
  }

  .parallax__layer__3 {
    transform: translateZ(-50px) scale(1.5);
  }

  .parallax__layer__4 {
    transform: translateZ(-25px) scale(1.25);
  }

  .parallax__layer__5 {
    transform: translateZ(-10px) scale(1.1);
  }

  .parallax__layer__6 {
    transform: translateZ(0px) scale(1);
  }
}

/* iPhone 14 Pro Max or similar */
@media only screen and (max-width: 430px) and (max-height: 932px) {
  .parallax {
    perspective: 40px;
    height: 100vh;
    min-height: -webkit-fill-available;
  }

  .parallax__layer__6 img {
    width: 110%;
    left: -5%;
    top: 15vh;
    height: 110vh;
  }

  .parallax__layer__5 img {
    width: 110%;
    left: -5%;
    top: 15vh;
    height: 110vh;
  }

  .parallax__layer__4 img {
    width: 110%;
    left: 25%;
    top: 15vh;
    height: 110vh;
  }

  .parallax__layer__3 img {
    width: 85%;
    left: 85%;
    top: 15vh;
    height: 110vh;
  }

  .parallax__layer__2 img {
    width: 33%;
    left: 55%;
    top: 32vh;
  }

  .parallax__layer__1 img {
    width: 100%;
    left: -37%;
    top: 6vh;
  }

  .parallax__layer__0 img {
    width: 35%;
    left: 85%;
    top: 33vh;
  }

  .parallax__layer__0 {
    transform: translateZ(-150px) scale(2.5);
  }

  .parallax__layer__1 {
    transform: translateZ(-100px) scale(2);
  }

  .parallax__layer__2 {
    transform: translateZ(-60px) scale(1.6);
  }

  .parallax__layer__3 {
    transform: translateZ(-30px) scale(1.3);
  }

  .parallax__layer__4 {
    transform: translateZ(-15px) scale(1.15);
  }

  .parallax__layer__5 {
    transform: translateZ(-5px) scale(1.05);
  }
}

/* Cover section */
.parallax__cover {
  background-color: #2D112B;
  background-image: url('./assets/IMG18.jpg');
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;

  position: absolute;
  top: 100%;
  left: 0;
  right: 0;

  min-height: 100vh;
  /* mínimo = una pantalla */
  height: auto;
  /* crece si el contenido lo requiere */

  z-index: 2;
}



